import Vue from 'vue';
import Router from 'vue-router';

const Home = () => import('@/router/Home/index');

Vue.use(Router);

const routes = [
  {
    path: '/',
    alias: '/security',
    name: 'home',
    component: Home,
    meta: { title: '防伪查询' },
  },
];

let router = new Router({
  // mode: 'history',
  routes
});

router.beforeEach((to, from, next) => {
  // 路由发生变化修改页面 title
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  next();
});

export default router;



